<template>
  <view class="edit-profile-container">
    <view class="form-item">
      <text class="label">用户名</text>
      <input class="input" v-model="username" placeholder="请输入新的用户名" />
    </view>

    <button class="save-btn" @click="saveChanges">保存修改</button>

    <button class="change-password-btn" @click="goToChangePassword">修改密码</button>
  </view>
</template>

<script>
export default {
  data() {
    return {
      username: this.$store.state.user.userInfo.nickname || ''
    }
  },
  methods: {
    saveChanges() {
      if (!this.username) {
        uni.showToast({
          title: '用户名不能为空',
          icon: 'none'
        })
        return
      }
      // 假设有一个API可以更新用户名
      this.$store.dispatch('user/updateUsername', this.username)
        .then(() => {
          uni.showToast({
            title: '修改成功',
            icon: 'success'
          })
        })
        .catch(() => {
          uni.showToast({
            title: '修改失败',
            icon: 'none'
          })
        })
    },
    goToChangePassword() {
      uni.navigateTo({
        url: '/pages/mine/changePassword'
      })
    }
  }
}
</script>

<style scoped>
.edit-profile-container {
  padding: 20rpx;
}

.form-item {
  margin-bottom: 20rpx;
}

.label {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 10rpx;
}

.input {
  width: 100%;
  padding: 10rpx;
  font-size: 28rpx;
  border: 1rpx solid #ccc;
  border-radius: 8rpx;
}

.save-btn, .change-password-btn {
  width: 100%;
  padding: 20rpx;
  font-size: 28rpx;
  color: #fff;
  background-color: #FF6B01;
  border-radius: 8rpx;
  text-align: center;
  margin-top: 20rpx;
}

.change-password-btn {
  background-color: #FF4D4F;
}
</style> 